.awards-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 7.5rem;
    padding: 7.5rem 0;
    .box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 70%;
        gap: 5rem;
        //margin-top: 3%;
        padding: 3%;
        background-color: $colorWhite;
        border-radius: 2rem;
        box-shadow: 0.375rem 0.375rem 2.5rem 0.75rem $colorGray300;
        font-family: "Noto Sans KR";
        color: $colorBrown100;
        img {
            width: 50%;
            //margin-top: 5%;
            //margin-bottom: 3%;
        }
        div {
            font-size: 2rem;
            display: flex;
            justify-content: center;
            align-items: center;
            //margin-bottom: 3%;
        }
    }
}